:root {
  --font-josefin: "Josefin Sans", sans-serif;
}

body {
  font-family: var(--font-josefin);
  font-size: 18px;
}

:root {
  --bright-blue: hsl(220, 98%, 61%);
  --checkbackground-start: hsl(192, 100%, 67%);
  --checkbackground-end: hsl(280, 87%, 65%);
  --very-light-gray-l: hsl(0, 0%, 98%);
  --very-light-grayish-blue-l: hsl(236, 33%, 92%);
  --light-grayish-blue-l: hsl(233, 11%, 84%);
  --dark-grayish-blue-l: hsl(236, 9%, 61%);
  --very-dark-grayish-blue-l: hsl(235, 19%, 35%);
  --very-dark-blue: hsl(235, 21%, 11%);
  --very-dark-desaturated-blue: hsl(235, 24%, 19%);
  --light-grayish-blue: hsl(234, 39%, 85%);
  --light-grayish-blue-hover: hsl(236, 33%, 92%);
  --dark-grayish-blue: hsl(234, 11%, 52%);
  --very-dark-grayish-blue: hsl(233, 14%, 35%);
  --very-dark-grayish-blue2: hsl(237, 14%, 26%);
  --background: var(--very-light-grayish-blue-l);
  --task-bg: var(--very-light-gray-l);
  --text-active: var(--very-dark-grayish-blue-l);
  --text-inactive: var(--light-grayish-blue-l);
  --task-border: var(--light-grayish-blue-l) ;
}

@media (prefers-color-scheme: dark) {
  :root {
    --background: var(--very-dark-blue);
    --task-bg: var(--very-dark-desaturated-blue);
    --text-active: var(--light-grayish-blue);
    --text-inactive: var(--dark-grayish-blue);
    --task-border: var(--dark-grayish-blue) ;
  }
}
body.light {
  --background: var(--very-light-grayish-blue-l);
  --task-bg: var(--very-light-gray-l);
  --text-active: var(--very-dark-grayish-blue-l);
  --text-inactive: var(--light-grayish-blue-l);
  --task-border: var(--light-grayish-blue-l);
  --background-img-mobile: url("/images/bg-mobile-light.jpg");
  --background-img-desktop: url("/images/bg-desktop-light.jpg");
}
body.light #light {
  display: none;
}

body.dark {
  --background: var(--very-dark-blue);
  --task-bg: var(--very-dark-desaturated-blue);
  --text-active: var(--light-grayish-blue);
  --text-inactive: var(--dark-grayish-blue);
  --task-border: var(--dark-grayish-blue);
  --background-img-mobile: url("/images/bg-mobile-dark.jpg");
  --background-img-desktop: url("/images/bg-desktop-dark.jpg");
}
body.dark #dark {
  display: none;
}

:root {
  --border-radius-size: 0.25rem;
  --background-img-mobile: url("/images/bg-mobile-light.jpg");
  --background-img-desktop: url("/images/bg-desktop-light.jpg");
}

@media (prefers-color-scheme: dark) {
  :root {
    --background-img-mobile: url("/images/bg-mobile-dark.jpg");
    --background-img-desktop: url("/images/bg-desktop-dark.jpg");
  }
}
html {
  font-size: 100%;
  box-sizing: border-box;
}

body {
  margin: 0;
  padding: 0;
}
body.noscroll {
  overflow: hidden;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

ul {
  list-style-type: none;
  margin: 0;
  padding: 0;
}

.flex {
  display: flex;
}
.flex-jc-sb {
  justify-content: space-between;
}
.flex-jc-c {
  justify-content: center;
}
.flex-ai-c {
  align-items: center;
}
.flex-ai-b {
  align-items: baseline;
}
.flex-d-c {
  flex-direction: column;
}
.flex-d-r {
  flex-direction: row;
}

@media (max-width: 49.9375em) {
  .hide-for-mobile {
    display: none;
  }
}

@media (min-width: 50em) {
  .hide-for-desktop {
    display: none;
  }
}

.visually-hidden {
  position: absolute;
  left: -10000px;
  top: auto;
  width: 1px;
  height: 1px;
  overflow: hidden;
}

.img-responsive {
  max-width: 100%;
  height: auto;
}

button {
  padding: 0;
  margin: 0;
  border: none;
  background-color: transparent;
  cursor: pointer;
}

input[type=text] {
  border: none;
  border-radius: 0;
  outline: none;
}
input[type=text]:focus {
  border: none;
}

.border-r-all {
  border-radius: var(--border-radius-size);
}
.border-r-bottom {
  border-bottom-left-radius: var(--border-radius-size);
  border-bottom-right-radius: var(--border-radius-size);
}

h1,
h2,
h3 {
  margin-top: 0;
}

h1 {
  color: var(--very-light-gray-l);
  letter-spacing: 0.625rem;
  font-size: 1.375rem;
}
@media (min-width: 50em) {
  h1 {
    letter-spacing: 1rem;
    font-size: 2rem;
  }
}

a,
a:visited,
a:active {
  text-decoration: none;
}

p {
  margin: 0;
}

.container {
  padding: 0 3.75rem;
  max-width: 69.375rem;
}

.main {
  height: 100vh;
  position: relative;
  background-color: var(--background);
  background-image: var(--background-img-mobile);
  background-position: top;
  background-repeat: no-repeat;
  background-size: 100% 33%;
  display: flex;
  align-items: center;
  flex-direction: column;
}
@media (min-width: 50em) {
  .main {
    background-image: var(--background-img-desktop);
  }
}

.app {
  position: absolute;
  margin-top: 6.25rem;
  width: 90%;
}
@media (min-width: 50em) {
  .app {
    width: 40%;
  }
}
.app .item {
  display: flex;
  align-items: center;
  background-color: var(--task-bg);
  padding: 0.3125rem 1.25rem;
  min-height: 3.125rem;
}
.app__main .task {
  border-bottom: 1px solid var(--task-border);
  color: var(--text-active);
}
.app__main .task:hover .delete_btn {
  opacity: 1;
}
.app__main .task.completed {
  text-decoration: line-through;
  color: var(--text-inactive);
}
.app__main .task p {
  flex-grow: 1;
}
.app__main .task .delete_btn {
  float: right;
  margin-left: auto;
  position: relative;
}
@media (min-width: 50em) {
  .app__main .task .delete_btn {
    opacity: 0;
  }
}
.app__main .task .delete_btn:before {
  content: url("/images/icon-cross.svg");
  position: absolute;
  top: -0.625rem;
  right: 0;
}
.app__main #add_task {
  margin-bottom: 0.9375rem;
}
.app__main #add_task input[type=text] {
  width: 100%;
  background-color: var(--task-bg);
  color: var(--text-active);
  font-family: inherit;
  font-size: inherit;
}
.app__footer {
  height: 3.125rem;
  background-color: var(--task-bg);
  font-size: 1.0625rem;
  font-family: inherit;
  padding: 0 1.25rem;
  color: var(--text-inactive);
  margin-bottom: 0.9375rem;
}
.app__footer .items_count {
  font-size: 0.8125rem;
}
.app__footer .clear_btn {
  color: var(--text-inactive);
}
.app__footer .clear_btn:hover {
  color: var(--text-active);
}
.app__filters a {
  color: var(--text-inactive);
}
.app__filters a:hover {
  color: var(--text-active);
}
.app__filters a:focus {
  color: var(--bright-blue);
}
.app__filters a:not(:last-child) {
  margin-right: 0.9375rem;
}
.app .tip {
  text-align: center;
  color: var(--text-inactive);
  margin: 3.125rem 0;
}

input[type=checkbox] {
  appearance: none;
  margin: 0;
  font: inherit;
  color: currentColor;
  width: 1.375rem;
  height: 1.375rem;
  border: 0.0625rem solid var(--text-inactive);
  border-radius: 50%;
  transform: translateY(-0.075em);
  cursor: pointer;
  display: grid;
  place-content: center;
  margin-right: 1.25rem;
  flex-shrink: 0;
}
input[type=checkbox].completed {
  background-image: linear-gradient(125deg, var(--checkbackground-start), var(--checkbackground-end));
}
input[type=checkbox].completed::before {
  transform: scale(1);
}
input[type=checkbox]::before {
  content: "";
  background-image: url("/images/icon-check.svg");
  background-repeat: no-repeat;
  background-position: center;
  width: 0.65em;
  height: 0.65em;
  transform: scale(0);
  transform-origin: bottom left;
  transition: 120ms transform ease-in-out;
}
input[type=checkbox]:focus {
  outline: max(2px, 0.15em) solid currentColor;
  outline-offset: max(2px, 0.15em);
}
input[type=checkbox]:disabled {
  cursor: not-allowed;
}